import React from "react";

function LanguageLemmata(props) {
  const lemmata = props.lemmata;
  return (
    <>
      <ol className="langaugeLemmataList">
        {lemmata.map(lemma => <li key={lemma.lemmaId}>
          <a className="languageLemmata" href="#">
            {lemma.original} {lemma.translation}
          </a>
        </li>)}
      </ol>
    </>
  );
}

export default LanguageLemmata;