import React from "react";

function SetInitialLemmata(props) {
  const lemmata = props.lemmata;
  
  let msg = 'No lemmata loaded.';
  if (lemmata) {
    msg = 'Saved lemmata loaded successfully. '
      + lemmata.length + ' lemma' + (lemmata.length > 1 ? 'ta' : '') + ' in total.';
  }
  
  return (
    <p>{msg}</p>
  );
}

export default SetInitialLemmata;